.rating-container {
  display: flex;
  height: 100vh;
  align-items: center;
  justify-content: center;
  background: #121417;
  color: white;
  font-family: Arial, sans-serif;
}

.card {
  background-color: #1e2126;
  padding: 2rem;
  border-radius: 12px;
  text-align: center;
  width: 300px;
}

.ratings {
  display: flex;
  justify-content: space-between;
  margin: 1rem 0;
}

.rating-btn {
  background-color: #2c2f36;
  color: white;
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  font-size: 1rem;
  cursor: pointer;
  transition: 0.3s;
}

.rating-btn:hover,
.rating-btn.selected {
  background-color: orange;
}

.submit-btn {
  margin-top: 1rem;
  padding: 0.6rem 2rem;
  background-color: orange;
  border: none;
  border-radius: 20px;
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: 0.3s;
}

.thank-you {
  padding: 2rem;
}
